<template>
	<div class="intro">
		<LineStackingIntro
			:sidebarVisible="true"
			:content="content"
			:storyActive="0"
			v-if="clientWidth > 768"
			dataset="./data/story-two-cultures/reduced/CLASSICS_THE_1990_2020_.json"
			:snippet="content.stories[0].snippets[0]"
			:statements="this.statements"
		/>
		<Share :content="content.social"/>

		<div class="wrapper">
			<h1 class="title"> {{ content.intro.title }} </h1>
			<h2 class="subtitle"> {{ content.intro.subtitle }} </h2>


			<div v-if="clientWidth > 600" class="intro--text-wrapper">
				<p class="intro--text"> {{ content.intro.p1 }} </p>
			</div>

			<span class="by">A project of <a class="link" href="https://metalabharvard.github.io/">metalab (at) Harvard</a> and part of <a class="link" href="https://curricle.berkman.harvard.edu/">Curricle research</a></span>

			<!-- <button @click="toggleIntroVisible" class="btn">Start</button> -->
		</div>

		<div class="stories-wrapper">
			<!-- Dieser Titel knnte hier noch gut rein -->
			<!-- <p class="">Data Stories: visualizing patterns in teaching and learning over time</p>  -->
			<div @click="dispatchCurrentStory(index)" class="story-outer" v-for="(story,index) in storiesArr" :key="story.id">
    			<span class="number">{{ `0${index + 1}` }}</span>
    			<span>{{ story.introduction.title }}</span>
    			<span class="subtitle">{{ introContent[index] }}</span>
			</div>
		</div>
	</div>
</template>

<script>
	import Share from './Share.vue';
	import LineStackingIntro from './LineStackingIntro.vue';
	import { select as d3Select } from 'd3';


	export default {
		name: 'Intro',
		props: ['content'],
		data() {
			return {
				introContent: {
					0: 'Investigates patterns of course offerings in the sciences and the humanities, arguing for discernible differences in approaches to teaching and learning between what C. P. Snow called the "two cultures" of academic life.',
					1: 'Examines the establishment of a popular musical genre as a topic of scholarly study, as it makes its way from controversy into the curriculum.',
					2: 'Seeks out the surprising impact of the material turn in literary studies across disciplines, from the arts and humanities to the social sciences and caring professions.',
					3: 'Tracing two famous authors though their own course choices, and the impact they have on teaching today.',
					4: 'Looks at the rise of this preeminent Harvard program, and the invisible curriculum sought outand protestedby black students at Harvard before its founding.'
				},
				statements: {
					year: 'year',
					lineGroup: 'externalCourseId',
					keyword: 'COMPUTER_SCIENCE',
					keywordTitle: 'Computer Science',
					startYear: 1990,
					endYear: 2010,
					searchMode: "department",
					areaGroup: 'component', // subjectDescription // component // externalCourseId
					maxCall: 10000
				}
			}
		},
		components: {
			Share,
			LineStackingIntro
		},
		computed: {
            clientWidth: () => {
                return d3Select('#app').node().getBoundingClientRect().width;
            },
            clientHeight: () => {
                return d3Select('#app').node().getBoundingClientRect().height;
			},
			storiesArr() {
				return this.content.stories;
			}
		},
		methods: {
			dispatchIntroVisible(state) {
				this.$store.dispatch('SET_INTRO_VISIBLE', state);
			},
			toggleIntroVisible() {
				d3Select('.intro').classed('hide', true);
				setTimeout(() => {
					this.dispatchIntroVisible(false);
				}, 500)
			},
            dispatchCurrentStory(state) {
				d3Select('.intro').classed('hide', true);
				d3Select('.Nav').style('display', 'flex');
				setTimeout(() => {
					this.dispatchIntroVisible(false);
				}, 500)
                this.$store.dispatch("SET_CURRENT_STORY", state);
            },
		},
		created() {
			d3Select('.intro').style('width', this.clientWidth);
			d3Select('.intro').style('height', this.clientHeight);
		},
		mounted() {
			setTimeout(() => {
				d3Select('.title').classed('visible', true)
			}, 0)
			setTimeout(() => {
				d3Select('.subtitle').classed('visible', true)
			}, 0)
			setTimeout(() => {
				d3Select('.intro--text').classed('visible', true)
				d3Select('.btn').classed('visible', true)
				d3Select('.by').classed('visible', true)
			}, 100)
		}
	}
</script>

